.main {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  background: no-repeat center center;
  background-image: url("../../img/home-bg.jpg");
  background-size: cover;
  margin-bottom: 50px;
  height: 450px;
  color: white;
  position: relative;
}
.main :nth-child(2) {
  text-align: center;
}
hr.small {
  max-width: 100px;
  border-width: 4px;
  border-color: white;
}
.main h1,
.h1 {
  font-size: 84px;
  margin-bottom: 0;
  margin-top: 80px;
  font-weight: 700;
}
p,
.p {
  font-size: 26px;
  font-weight: 400;
  margin: 20px 0 0 0;
}
input[type="text"] {
  background: transparent;
  border: none;
  outline: none;
  width: 90%;
  text-align: center;
}

@media screen and (max-width: 768px) {
  .main h1,
  .h1 {
    margin-top: 60px;
    font-size: 50px;
  }
  p,
  .p {
    font-size: 18px;
    margin-top: 15px;
  }
  .main {
    height: 300px;
    margin-bottom: 0;
  }
  hr {
    margin: 10px auto;
  }
}
